@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
.app-header{margin-top:3rem}.app-header__content{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;border:4px #606e85 solid;padding:1rem 2rem;border-radius:15px}.app-header__content-score{line-height:1;padding:0.7rem;width:180px;border-radius:15px;background-color:#fbfbfb;text-align:center}.app-header__content-score .score_label{font-weight:600;font-size:1.5rem;color:#2a46c0}.app-header__content-score .score_point{font-size:5rem;color:#59556c}@media (max-width: 425px){.app-header__logo{max-width:70px}.app-header__content-score{width:110px}}

.app-interface{margin-top:10rem;text-align:center}.app-interface__field{display:inline-block;position:relative;min-height:400px}.app-interface__wrapper{width:120px;height:120px;position:absolute;border-radius:50%;cursor:pointer}.app-interface__wrapper:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.app-interface__wrapper:nth-child(2){top:52px;left:-52px}.app-interface__wrapper:nth-child(3){top:-60px;left:110px}.app-interface__wrapper:nth-child(4){top:52px;left:261px}.app-interface__wrapper:nth-child(5){top:247px;left:210px}.app-interface__wrapper:nth-child(6){top:247px;left:0}@media (max-width: 767px){.app-interface__wrapper{width:80px;height:80px}.app-interface__wrapper:nth-child(2){top:56px;left:-32px}.app-interface__wrapper:nth-child(3){top:-45px;left:95px}.app-interface__wrapper:nth-child(4){top:56px;left:228px}.app-interface__wrapper:nth-child(5){top:215px;left:185px}.app-interface__wrapper:nth-child(6){top:215px;left:10px}.app-interface__field>img{max-width:270px}}@media (max-width: 425px){.app-interface__field{min-height:220px}.app-interface__wrapper{width:70px;height:70px}.app-interface__wrapper:nth-child(2){top:35px;left:-25px}.app-interface__wrapper:nth-child(3){top:-36px;left:63px}.app-interface__wrapper:nth-child(4){top:35px;left:155px}.app-interface__wrapper:nth-child(5){top:150px;left:126px}.app-interface__wrapper:nth-child(6){top:150px;left:0}.app-interface__field>img{max-width:200px}}

.game-item{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border-radius:50%;box-shadow:inset 0 -5px 0 0 rgba(0,0,0,0.3);transition:all .4s;-webkit-animation:fadeIn .4s;animation:fadeIn .4s}.game-item .game-item__content{width:80%;height:80%;border-radius:50%;background-color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;box-shadow:inset 0 5px 0 0 rgba(0,0,0,0.3)}.game-item .game-item__content img{width:50%;height:auto}.spock{background-image:linear-gradient(0deg, #40b9ce 0%, #52bed1 100%)}.scissors{background-image:linear-gradient(0deg, #ec9e0e 0%, #eca922 100%)}.paper{background-image:linear-gradient(0deg, #4865f4 0%, #5671f5 100%)}.rock{background-image:linear-gradient(0deg, #dc2e4e 0%, #dd405d 100%)}.lizard{background-image:linear-gradient(0deg, #834fe3 0%, #8c5de5 100%)}

.app-footer__rules{display:block;background:none;border:1px solid #fff;border-radius:4px;width:100px;margin-left:auto;line-height:2;transition:all .4s;margin-top:2rem}.app-footer__rules:hover{background:#fff;color:#000}@media (max-width: 425px){.app-footer{margin-top:4rem}.app-footer__rules{margin:0 auto}}

.overlay{z-index:9;position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.3);-webkit-animation:fadeIn .6s;animation:fadeIn .6s}.modal{background-color:#fff;width:400px;height:450px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:15px;padding:2.3rem 2rem}.modal__header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.modal__header-title{font-weight:700}.modal__header-close{cursor:pointer}.modal__content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.close-mobile{display:none}@media (max-width: 425px){.modal{width:100vw;height:100vh;border-radius:0}.modal__header{-webkit-justify-content:center;justify-content:center;font-size:2.5rem}.modal__content{margin-top:4.8rem}.modal__content-rules{width:300px}.close-desktop{display:none}.close-mobile{margin-top:5rem;display:block}}

.game-process{margin-top:3rem}.game-process-selection{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;transition:all .4s;-webkit-animation:fadeIn .8s;animation:fadeIn .8s}.game-process-result{text-align:center;-webkit-animation:fadeIn .8s;animation:fadeIn .8s}.game-process__item{width:250px;height:250px;margin:3rem auto 0;border-radius:50%;position:relative}.game-process__item .winner-indicator__wrapper{width:100%;height:100%;position:absolute;left:0;top:0;-webkit-transform:scale(0);transform:scale(0);z-index:-1}.game-process__item .winner-indicator__wrapper.indicator_active{transition:all .6s;-webkit-transform:scale(1.4);transform:scale(1.4)}.empty-item{width:100%;height:100%;background-color:rgba(0,0,0,0.4);border-radius:50%}@media (max-width: 992px){.game-process__item{width:150px;height:150px}}@media (max-width: 425px){.game-process-selection{-webkit-flex-direction:column-reverse;flex-direction:column-reverse}.game-process__item{width:110px;height:110px}}

.game-result{padding-top:150px}.game-result__title{font-size:4.5rem}.game-result__reset{width:150px;height:45px;border:none;border-radius:4px;color:#000}.game-result__reset:hover{color:#ac0000}@media (max-width: 767px){.game-result{padding-top:10px}}

.winner-indicator{position:relative;width:100%;height:100%;border-radius:50%;background:rgba(255,255,255,0.1)}.winner-indicator div{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.winner-indicator__middle{border-radius:50%;background-color:rgba(255,255,255,0.05);width:90%;height:90%}.winner-indicator__inner{border-radius:50%;background-color:rgba(255,255,255,0.02);width:85%;height:85%}

* {
    font-family: 'Barlow Semi Condensed', sans-serif;
    color: #fff;
}

body {
    background: rgb(31, 55, 86);
    background: radial-gradient(circle at 50% 0, hsl(214, 47%, 23%) 0%, hsl(237, 49%, 15%) 100%);
    background-repeat: no-repeat;
    min-height: 105vh;
    padding: 3rem 0 7.5rem;
}

img {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.dark {
    color: hsl(229, 25%, 31%);
}

@media (max-width: 425px) {
    html {
        font-size: 11px;
    }
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
